export function objectHasValue(input: { [key: string]: any }): boolean {
  for (const property in input) {
    if (typeof input[property] === 'object') {
      return objectHasValue(input[property]);
    } else if (input[property]) {
      return true;
    }
  }
  return false;
}

export function objectsHasValueByKey(
  obj: { [key: string]: any } | undefined,
  keys: string[]
): any {
  if (!obj) {
    return;
  }
  return keys.reduce((acc: { [key: string]: any }, key, index) => {
    if (!acc || !acc.hasOwnProperty(key)) {
      return false;
    }

    const isLast = keys.length - 1 === index;

    if (isLast && typeof acc[key] === 'object') {
      return objectHasValue(acc[key]) && acc[key];
    }

    return acc[key];
  }, obj);
}
